.fruitbasket-block {
  margin: 4px;
  padding: 12px;
}

.fruitbasket-block h2 {
  color: #fff;
  margin: 0 0 8px;
  padding: 0;
  font-size: 28px;
  font-weight: 400;
}

.fruitbasket-block p {
  color: #fffd;
  margin: 4px 0 0;
  padding: 0;
  font-weight: 400;
}

.block-blue {
  background-color: #7a82d1;
}

.block-green {
  background-color: #79c379;
}

.block-red {
  background-color: #ff4646;
}

.fruitbasket-lower {
  flex-flow: row;
  display: flex;
}

.contact, .cool-people {
  flex: 1;
}

.link-grid {
  flex-flow: wrap;
  justify-content: center;
  align-items: flex-start;
  display: flex;
}

.link-grid-item {
  width: 25%;
  height: 0;
  max-width: 25%;
  padding-bottom: 25%;
}

.link-grid-img-container {
  width: calc(100% - 8px);
  height: 0;
  padding-bottom: calc(100% - 8px);
  position: relative;
}

.link-grid-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 50%;
  position: absolute;
  top: 4px;
  left: 4px;
}

.link-grid-item-cover {
  width: 100%;
  height: 100%;
  opacity: 0;
  background-color: #fff;
  border-radius: 50%;
  transition: opacity .2s;
  position: absolute;
  top: 4px;
  left: 4px;
}

.link-grid-item-cover:hover {
  opacity: .4;
}

@media only screen and (max-width: 768px) {
  .fruitbasket-lower {
    flex-direction: column;
  }
}

